@charset "UTF-8";
@keyframes rotateAnimation {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes slideLeft {
  0% {
    transform: translateX(150%) scale(1.5);
  }
  100% {
    transform: none;
  }
}
