body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}

:focus {
  outline: 0;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  display: block;
}

a:hover {
  text-decoration: none;
}

html,
body {
  height: 100%;
}

input,
textarea {
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
}

label,
button,
input[type=submit],
input[type=button] {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

img {
  max-width: 100%;
  vertical-align: middle;
  height: auto;
}

body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

button,
input[type=submit] {
  -webkit-appearance: none;
  -moz-appearance: none;
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "Travels";
  font-style: normal;
  font-weight: 300;
  src: local("Travels"), url("../fonts/TTTravels-Light.ttf") format("truetype");
}
@font-face {
  font-family: "Travels";
  font-style: normal;
  font-weight: 400;
  src: local("Travels"), url("../fonts/TTTravels-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Travels";
  font-style: normal;
  font-weight: 500;
  src: local("Travels"), url("../fonts/TTTravels-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Travels";
  font-style: normal;
  font-weight: 600;
  src: local("Travels"), url("../fonts/TTTravels-DemiBold.ttf") format("truetype");
}
@font-face {
  font-family: "Travels";
  font-style: normal;
  font-weight: 700;
  src: local("Travels"), url("../fonts/TTTravels-Bold.ttf") format("truetype");
}
.fp-watermark {
  display: none;
}

html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  position: relative;
  min-width: 320px;
  margin: auto;
  background-color: #E5E5E5;
  font-family: "Travels", sans-serif;
}

a {
  display: block;
}

h1 {
  font-size: 76px;
  font-weight: 400;
  line-height: 110%;
}

h2 {
  font-size: 44px;
  font-weight: 400;
}

h4 {
  font-size: 24px;
  font-weight: 500;
  line-height: 128%;
}

p {
  font-size: 18px;
  line-height: 140%;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  width: 91.6666666667%;
}

.row {
  display: flex;
}

.logo {
  cursor: pointer;
  max-width: 98px;
}
.logo img {
  object-fit: contain;
  width: 100%;
}

.switch {
  background-color: #FFFFFF;
  border: 1px solid #E2E8F0;
  padding: 3px;
  border-radius: 4rem;
  display: flex;
  gap: 4px;
  font-weight: 500;
  font-size: 14px;
  line-height: 1;
  cursor: pointer;
}
.switch__btn {
  padding: 8px 12px;
  transition: 0.3s ease-out all;
  color: #636C81;
  border-radius: inherit;
}
.switch__btn.active {
  background-color: #5A30F0;
  color: #FFFFFF;
}

.header {
  padding: 16px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}
.header__container {
  max-width: 1380px;
  width: 95.8333333333%;
  padding-left: 25px;
}
.header__row {
  gap: 10px;
  align-items: center;
  justify-content: space-between;
}
.header__left-content {
  display: flex;
  align-items: center;
  column-gap: 20px;
}
.header__right-content {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.header__contacts {
  display: flex;
  align-items: center;
  gap: 15px;
  font-weight: 500;
}
.header__phone {
  cursor: pointer;
  color: #404040;
  transition: 0.3s ease-in-out all;
}
.header__phone:hover {
  color: #5A30F0;
}
.header__working-days {
  display: flex;
  align-items: center;
  gap: 15px;
}
.header__working-days::before {
  content: "";
  width: 2px;
  height: 14px;
  background-color: #404040;
}
.header__burger-menu {
  margin-left: 2.2222222222vw;
}

.menu__list {
  display: flex;
  align-items: center;
  column-gap: 2.2222222222vw;
  row-gap: 7px;
  justify-content: space-between;
  flex-wrap: wrap;
}
.menu__list-item a {
  font-size: 16px;
  color: #404040;
  font-weight: 500;
  position: relative;
}
.menu__list-item a::before {
  content: "";
  position: absolute;
  bottom: -5px;
  right: 0;
  width: 0;
  background: #5A30F0;
  border-radius: 2px;
  height: 2px;
  transition: 0.3s ease all;
}
.menu__list-item a:hover::before {
  width: 100%;
  left: 0;
  right: auto;
}

.burger-menu__icon {
  width: 32px;
  height: 32px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 110;
}
.burger-menu__icon::before, .burger-menu__icon::after {
  content: "";
  position: absolute;
  width: 22px;
  height: 2px;
  background-color: #404040;
  border-radius: 1rem;
  transition: 0.3s ease-in-out all;
}
.burger-menu__icon::before {
  transform: translateY(-4px);
}
.burger-menu__icon::after {
  transform: translateY(4px);
}
.burger-menu__icon.active::before {
  transform: rotate(45deg) translateY(0);
}
.burger-menu__icon.active::after {
  transform: rotate(-45deg) translateY(0);
}
.burger-menu__inner {
  position: fixed;
  inset: 0 auto 0 0;
  height: 100%;
  background-color: #5A30F0;
  padding: 30px;
  width: 300px;
  transition: 0.5s ease-in-out all;
  transform: translateX(-120%);
}
.burger-menu__inner.active {
  transform: none;
}
.burger-menu__logo {
  background-color: #E5E5E5;
  padding: 5px 8px;
  border-radius: 0.5rem;
  margin-bottom: 30px;
}
.burger-menu__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid #FFFFFF;
}
.burger-menu__list-item {
  display: flex;
  justify-content: flex-start;
}
.burger-menu__list-item a {
  font-size: 20px;
  color: #FFFFFF;
  font-weight: 600;
  transition: 0.3s ease-in-out all;
}
.burger-menu__list-item a:hover {
  color: #cccccc;
  transform: translateX(5px);
}
.burger-menu__phone {
  margin-top: 15px;
  font-size: 18px;
  font-weight: 500;
  color: #FFFFFF;
  transition: 0.3s ease-in-out all;
}
.burger-menu__phone:hover {
  color: #cccccc;
}

.hero {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.hero__left-bg {
  width: 100%;
  height: 100%;
  position: relative;
}
.hero__left-bg img {
  position: absolute;
  inset: 0;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.hero__container {
  align-self: center;
  padding: 100px 0 100px;
  width: 56%;
}
.hero__description {
  margin-top: 30px;
}

.sticky-area {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 300;
}

.btn-circle {
  width: 195px;
  height: 195px;
  background-color: #5A30F0;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  cursor: pointer;
  position: absolute;
  z-index: 350;
  margin: 0 auto;
  transition: 1s ease-in-out all;
}
.btn-circle__text {
  font-weight: 500;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
  transform: rotate(-15deg);
  animation: rotateAnimation 30s linear infinite;
}
.btn-circle.hide {
  transform: scale(0.9) translateY(80px);
}
.btn-circle.remove {
  transform: scale(0);
  transform-origin: bottom center;
}

.features {
  padding: 75px 0;
}
.features__list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 40px;
}
.features__list-item {
  transition: 0.5s ease-in-out all;
  transform: translateY(100px);
  display: flex;
}
.features__list-item.fadeIn {
  transform: none;
}
.features__list-item:nth-child(1) {
  transition-delay: 0.1s;
}
.features__list-item:nth-child(2) {
  transition-delay: 0.3s;
}
.features__list-item:nth-child(3) {
  transition-delay: 0.5s;
}
.features__list-item:nth-child(4) {
  transition-delay: 0.7s;
}
.features__item-wrapper {
  padding: 20px 35px 0;
  position: relative;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: #F3F6FB;
  transition: 0.3s ease-in-out all;
  width: 100%;
  height: 100%;
}
.features__item-wrapper:hover {
  background-color: #5A30F0;
  transition: 0.3s ease-in-out all;
}
.features__item-wrapper:hover .features__item-title,
.features__item-wrapper:hover .features__item-img {
  opacity: 0;
  visibility: hidden;
}
.features__item-wrapper:hover .features__item-description {
  opacity: 1;
  visibility: visible;
}
.features__item-title {
  margin-bottom: 62px;
  text-align: center;
  transition: inherit;
}
.features__item-img {
  max-height: 175px;
  transition: inherit;
}
.features__item-img img {
  object-fit: contain;
}
.features__item-description {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 30px 30px 50px;
  left: 0;
  top: 0;
  opacity: 0;
  visibility: hidden;
  transition: inherit;
  font-size: 16px;
  color: #FFFFFF;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #FFFFFF transparent;
}
.features__item-description::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.features__item-description::-webkit-scrollbar-track {
  background: transparent;
}
.features__item-description::-webkit-scrollbar-thumb {
  background-color: #FFFFFF;
  border-radius: 4px;
}
.features__item-description::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.features__item-description p {
  font-size: inherit;
}

.how-we-work {
  padding: 75px 0 0;
  position: relative;
}
.how-we-work__container {
  max-width: 1094px;
  width: 75.9722222222%;
}
.how-we-work__row {
  justify-content: space-between;
  gap: 15px 30px;
  margin-bottom: 50px;
}
.how-we-work__description {
  padding-top: 15px;
  width: 38%;
}
.how-we-work__image {
  border-radius: 8px;
  max-width: 1094px;
  width: 75.9722222222%;
  margin: 0 auto;
  height: 43.75vw;
  transition: 1s ease-in-out all;
  position: relative;
  overflow: hidden;
}
.how-we-work__image img {
  height: 100%;
  width: 100%;
  border-radius: inherit;
  object-fit: cover;
  transition: 0.3s ease-in-out all;
}
.how-we-work__image.active {
  height: 100vh;
  width: calc(100% - 375px);
  max-width: initial;
  border-radius: 0;
}
.how-we-work__image-area {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  transition: 0.5s cubic-bezier(0.3, 0.3, 0.3, 1) all;
  z-index: 180;
}
.how-we-work__image-area.hide {
  z-index: 185;
}
.how-we-work__image-area.active {
  position: relative;
  animation: slideLeft 1s linear forwards;
  z-index: 190;
}
.how-we-work__menu {
  position: fixed;
  inset: 0 auto 0 0;
  z-index: 200;
  background-color: #5A30F0;
  padding: 35px 60px;
  display: flex;
  flex-direction: column;
  transition: 0.7s ease-in-out all;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #FFFFFF transparent;
  width: 375px;
  transform: translateX(-120%);
}
.how-we-work__menu::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.how-we-work__menu::-webkit-scrollbar-track {
  background: transparent;
}
.how-we-work__menu::-webkit-scrollbar-thumb {
  background-color: #FFFFFF;
  border-radius: 4px;
}
.how-we-work__menu::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.how-we-work__menu.slideIn {
  transform: none;
}
.how-we-work__menu.slideIn .how-we-work__menu-item {
  transform: none;
}
.how-we-work__menu.slideIn .how-we-work__menu-title {
  transform: none;
}
.how-we-work__menu-title {
  font-size: 16px;
  font-weight: 500;
  color: #FFFFFF;
  transition: 0.5s ease-in-out all;
  transition-delay: 0.4s;
  transform: translate(-50px, -50px);
}
.how-we-work__menu-list {
  display: flex;
  flex-direction: column;
  gap: 22px;
  flex: 1 0 auto;
  justify-content: center;
  margin: 30px 0;
}
.how-we-work__menu-item {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: 0.5s ease-in-out all;
  transform: translateX(-50px);
}
.how-we-work__menu-item:nth-child(1) {
  transition-delay: 0.3s;
}
.how-we-work__menu-item:nth-child(2) {
  transition-delay: 0.4s;
}
.how-we-work__menu-item:nth-child(3) {
  transition-delay: 0.5s;
}
.how-we-work__menu-item:nth-child(4) {
  transition-delay: 0.6s;
}
.how-we-work__menu-item a {
  display: flex;
  font-size: 44px;
  color: #FFFFFF;
  opacity: 0.7;
  transition: 0.7s ease all;
  cursor: pointer;
}
.how-we-work__menu-item span {
  opacity: 0;
  visibility: hidden;
  transition: inherit;
  font-style: italic;
  position: absolute;
  display: flex;
  align-items: center;
  left: 0;
  color: #FFFFFF;
}
.how-we-work__menu-item span::before {
  content: "→";
  display: flex;
  align-items: center;
  opacity: 0;
  width: 0;
  transition: inherit;
}
.how-we-work__menu-item:hover a {
  opacity: 1;
  color: transparent;
  visibility: hidden;
}
.how-we-work__menu-item:hover span {
  opacity: 1;
  visibility: visible;
}
.how-we-work__menu-item.active a {
  color: transparent;
  visibility: hidden;
}
.how-we-work__menu-item.active span {
  opacity: 1;
  visibility: visible;
}
.how-we-work__menu-item.active span::before {
  width: 100%;
  opacity: 1;
  margin-right: 10px;
}

@media only screen and (max-width: 1281px) {
  h1 {
    font-size: 64px;
  }
  .header__menu {
    display: none;
  }
  .features__item-wrapper {
    padding: 20px 15px 0;
  }
  .features__item-description {
    padding: 20px;
  }
  .how-we-work__image.active {
    width: calc(100% - 300px);
  }
  .how-we-work__menu {
    padding: 30px 45px;
    width: 300px;
  }
  .how-we-work__menu-title {
    transform: translate(-30px, -30px);
  }
  .how-we-work__menu-list {
    gap: 15px;
  }
  .how-we-work__menu-item a {
    font-size: 32px;
  }
}
@media only screen and (max-width: 1025px) {
  h1 {
    font-size: 50px;
  }
  h2 {
    font-size: 32px;
  }
  .container {
    width: 89%;
  }
  .header__container {
    padding-left: 0;
  }
  .hero__container {
    width: 70%;
  }
  .features__list {
    grid-template-columns: 1fr 1fr;
    grid-gap: 25px;
  }
  .how-we-work__description {
    padding-top: 0;
    width: 50%;
  }
  .how-we-work__image {
    width: 89%;
  }
  .how-we-work__image.active {
    width: calc(100% - 260px);
  }
  .how-we-work__menu {
    padding: 30px;
    width: 260px;
  }
  .how-we-work__menu-item a {
    font-size: 32px;
  }
}
@media only screen and (max-width: 769px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
  .burger-menu__icon.active::before, .burger-menu__icon.active::after {
    background-color: #FFFFFF;
  }
  .burger-menu__inner {
    width: 100%;
  }
  .btn-circle {
    width: 165px;
    height: 165px;
  }
  .features {
    padding: 50px 0;
  }
  .how-we-work {
    padding: 50px 0 0;
  }
  .how-we-work__row {
    flex-direction: column;
    margin-bottom: 30px;
  }
  .how-we-work__description {
    width: 100%;
  }
  .how-we-work__image {
    width: calc(100% - 30px);
  }
  .how-we-work__image.active {
    width: 100%;
  }
  .how-we-work__menu {
    padding: 30px 15px 15px;
    width: 100%;
    height: auto;
    inset: 0 0 auto 0;
    transform: translateY(-120%);
  }
  .how-we-work__menu-list {
    flex-direction: row;
    margin: 15px 0 0;
    justify-content: flex-start;
  }
  .how-we-work__menu-item a {
    font-size: 24px;
  }
  .how-we-work__menu-item span::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    background-color: #FFFFFF;
  }
}
@media only screen and (max-width: 651px) {
  .header__contacts {
    display: none;
  }
  .hero {
    display: block;
    padding-bottom: 50px;
    min-height: auto;
  }
  .hero__left-bg {
    height: 50vh;
    min-height: 250px;
    margin-bottom: 30px;
  }
  .hero__left-bg img {
    position: initial;
  }
  .hero__title br {
    display: none;
  }
}
@media only screen and (max-width: 551px) {
  .features__list {
    grid-template-columns: 1fr;
  }
  .features__list-item {
    transform: translateX(-100px);
  }
  .features__list-item:nth-child(1), .features__list-item:nth-child(2), .features__list-item:nth-child(3), .features__list-item:nth-child(4) {
    transition-delay: initial;
  }
  .how-we-work__image {
    min-height: 250px;
  }
}